import { yupResolver } from '@hookform/resolvers/yup'
import { Stack, TextField } from '@mui/material'
import { WorkflowAPI } from 'API/workflow'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'

interface FormInput {
	other: string
}
export default function DialogPage() {
	const nav = useNavigate()
	const { id } = useParams<{ id: string }>()
	const dispatch = useAppDispatch()
	const workflow = useAppSelector((s) => s.Workflow.list).find((f) => f.id === Number(id))
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)

	const schema = yup.object({
		other: yup.string().required(),
	})
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			other: '',
		},
	})
	useEffect(() => {
		if (!open || !workflow) {
			nav(-1)
		}
		return () => {
			setOpen(false)
		}
	}, [nav, open, workflow])
	if (!workflow) return null

	const onSubmit: SubmitHandler<FormInput> = async ({ other }) => {
		try {
			setLoading(true)
			const { error, message } = await WorkflowAPI.reject({
				id: workflow.id,
				other,
			})
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			setOpen(false)
		} finally {
			setLoading(false)
		}
	}
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => setOpen(false)}
			title="依頼却下確認"
			disabled={Object.keys(errors).length > 0}
			primaryButtonColor="error"
			primaryButtonText="却下する"
			maxWidth="md"
			handleSubmit={handleSubmit(onSubmit)}
		>
			<Stack>
				<TextField
					variant="outlined"
					autoFocus
					required
					margin="dense"
					fullWidth
					multiline
					minRows={4}
					size="small"
					label={'コメント'}
					placeholder="自由に入力してください。"
					InputLabelProps={{ shrink: true }}
					{...register('other')}
					error={'other' in errors}
				/>
			</Stack>
		</DialogConfirm>
	)
}
